function getReadableDate(date) {
    date = new Date(date);

    return ([
        date.getDate(),
        date.getMonth() + 1,
        date.getFullYear()
    ].join('.') + ' ' + [
        date.getHours(),
        date.getMinutes(),
        date.getSeconds()
    ].join(':')).replace(/\b(\d)\b/g, '0$1');
}

