function getEnvInfo() {
    var parsed = bowser._detect(navigator.userAgent),
        info = {
            browserName: parsed.name,
            browserVersion: parsed.version
        };

    if (parsed.mac) {
        info.os = 'Mac';
    } else if (parsed.windows) {
        info.os = 'Windows';
    } else if (parsed.linux) {
        info.os = 'Linux';
    }

    return info;
}

